.container {
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: rgb(25,25,25);
    overflow: auto;
}

::-webkit-scrollbar {
    width: 15px;
                
}

/* Track */
::-webkit-scrollbar-track {
    background: royalblue; 
}
 
/* Handle */
::-webkit-scrollbar-thumb {
    background: orange;
                
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: brown;
}
